export default {
	data() {
		return {
			title: 'Hello',
			info:{
				qq:"330729121",
				name:"Hello",
				info:"拿着最少的工资，做着最多的事情。",
				wx:"ai330729121",
				phone:"17602364646",
				www:"http://www.q05.cc/"
			},
			isLogin:false
		}
	},
	onLoad() {

	},
	onShow() {
		if(uni.getStorageSync("session")!="" && uni.getStorageSync("session")!=undefined && uni.getStorageSync("session").length>10){
			this.isLogin=true;
		}else{
			this.isLogin=false;
		}
	},
	methods: {
		//退出登录
		endLogin(){
			uni.clearStorage();
			uni.reLaunch({
				url:"../index/index"
			})
		},
		callPhone(e){
			uni.makePhoneCall({
			  phoneNumber: this.info.phone //仅为示例，并非真实的电话号码
			})
		},
		copy(e){
			var info=e.currentTarget.dataset.info;
			console.log(info);
			uni.setClipboardData({
			  data: info,
			  success (res) {
					uni.showToast({
						title:"已复制到剪切板"
					})
			  }
			})
		},
		adminLogin(e){
			uni.navigateTo({
				url:"./login"
			})
		}
	}
}
